import React, { useState, useEffect } from 'react';
import { Card, CardBody, CardHeader, Col, Row } from 'reactstrap';
import PROFILE_PICTURE from '../../assets/img/profile-image.png';
import { getGroups } from '../../handlers/Api/Calls';
import ReactTable from 'react-table-v6';
import { formatDistanceToNow } from 'date-fns';
import { ErrorToast } from '../../components';
import { Link } from 'react-router-dom';
import { GroupActions } from './components/GroupActions';
import './groups.scss';

const Groups = props => {
  const [data, setData] = useState([]);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    getGroups((apiLoading, apiData, apiError) => {
      setData(apiData);
      setError(apiError);
      setIsLoading(apiLoading);
    });
  }, []);
  
  const handleAddGroup = (newGroup) => {    
    setData(prevGroups => [newGroup, ...prevGroups]);
  };
  
  const handleEditGroup = (editedGroup) => {    
    const edit = (arr, item) => {
      return arr.map(group => {        
        if(group.id === item.id) {
          group = item;
        }
        return group;
      });
    }
    setData(prevGroups => edit(prevGroups, editedGroup))
  }
  
  const handleDeleteGroup = (groupID) => {
    setData(prevGroups => prevGroups.filter(group => {
      return group.id !== groupID;
    }))
  }

  return (
    <div className="animated fadeIn">
      <Row>
        <Col xl={12}>
          {error && <ErrorToast error={error} />}
          <Card>
            <CardHeader>
              <i className="fa fa-align-justify"></i> Groups <small className="text-muted">iEducate</small>
            </CardHeader>
            <CardBody>
              <div className="mb-2">
                <GroupActions 
                  showAdd
                  addCallback={handleAddGroup}
                />
              </div>

              <ReactTable
                data={data}
                loading={isLoading}
                defaultPageSize={10}
                className="-striped -highlight"
                filterable
                defaultFilterMethod={(filter, row) =>
                  !!String(row[filter.id]) && String(row[filter.id]).toLowerCase().includes(filter.value.toLowerCase())}
                columns={[
                  {
                    Header: "Basic",
                    columns: [
                      {
                        Header: "name",
                        accessor: "name",
                      },
                      {
                        Header: "Creator",
                        accessor: "edParty",
                        Cell: ({ original, value }) => (
                          <Link to={`/edParties/${original.edPartyId}`}>{value.name}</Link>
                        )
                      },
                      {
                        Header: "actions",
                        accessor: "id",
                        filterable: false,
                        width: 150,
                        Cell: ({ original }) => (
                          <GroupActions 
                            showDelete 
                            showEdit 
                            group={original} 
                            deleteCallback={handleDeleteGroup}
                            editCallback={handleEditGroup}
                          />
                        )
                      },
                    ]
                  }
                ]}
              />
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  )
}

export default Groups;
